import { Grid, TextField, Typography } from '@mui/material'
import React from 'react'

export default function ChartElement(props) {
    const name = props.name
    const [frequency, setFrequency] = React.useState("")
    const handleFrequencyChange = (event) => {
        setFrequency(parseFloat(event.target.value))
    }
    const [percentile, setPercentile] = React.useState("")
    const handlePercentileChange = (event) => {
        setPercentile(parseFloat(event.target.value))
    }

    React.useEffect(() => {
        const newState = { ...props.currentState }
        newState[name] = {freq: frequency, perc: percentile}
        props.setDict(newState);
    }, [frequency])

    React.useEffect(() => {
        const newState = { ...props.currentState }
        newState[name] = {freq: frequency, perc: percentile}
        props.setDict(newState);
    }, [percentile])

    React.useEffect(() => {
        props.setDict({})
    }, [])

    return <Grid container spacing={2} alignItems="center" justifyContent="middle">
        <Grid item xs={4}>
            <Typography style={{fontFamily: "Roboto Condensed", fontSize: "14"}}>{props.name.toUpperCase()}</Typography>
        </Grid>
        <Grid item xs={3}>
            <TextField variant="outlined" label="Frequency" onChange={handleFrequencyChange}></TextField>
        </Grid>
        <Grid item xs={3}>
            <TextField variant="outlined" label="Percentile" onChange={handlePercentileChange}></TextField>
        </Grid>
    </Grid>
}